import React, { Component } from 'react';
import './subHeader.css';
import Tab from "./tab"

class Subheader extends Component {
  render() {
    if (this.props.familyGroup == "all"){
      return (
        <div className='subHeader'>
            <Tab name="Your Ancestors" selected="true" linkTo=""/>
            <Tab name="Mother's Ancestors" linkTo=""/>
            <Tab name="Father's Ancestors"  linkTo=""/>
        </div>
    );
    } else if (this.props.familyGroup == "mother"){
      return (
        <div className='subHeader'>
            <Tab name="Your Ancestors" linkTo="" />
            <Tab name="Mother's Ancestors" selected="true" linkTo="" />
            <Tab name="Father's Ancestors" linkTo="" />
        </div>
    );
    } else {
      return (
        <div className='subHeader'>
            <Tab name="Your Ancestors" linkTo="" />
            <Tab name="Mother's Ancestors" linkTo="" />
            <Tab name="Father's Ancestors" selected="true" linkTo="" />
        </div>
    );
    }
  }
}

export default Subheader;
